@use "../styles.scss";

.app.light-mode .project-card {
    .project-source:hover {
        svg {
            border: 1px solid #000;
        }
    }

    .project-skill {
        border: 1px solid #999;
    }
}

.app.dark-mode .project-card {
    .project-source:hover {
        svg {
            border: 1px solid #fff;
        }
    }

    .project-skill {
        border: 1px solid #787878;
    }
}

.project-card .container {
    height: 100%;
    display: flex;
    flex-direction: column;

    .project-link {
        display: inline-block;
        position: absolute;
        text-decoration: none;
        color: inherit;
        height: 100%;
        width: 100%;
    }
    

    &:hover {
        img {
            transform: scale(1.05);
            -ms-transform: scale(1.05); /* IE 9 */
            -moz-transform: scale(1.05); /* Firefox */
            -webkit-transform: scale(1.05); /* Safari and Chrome */
            -o-transform: scale(1.05); /* Opera */
        }
    }

    a {
        color: inherit;
        text-decoration: none;
    }

    .project-image {
        position: relative;
        background-color: black;
        margin-bottom: 0.375rem;
        overflow: hidden;

        img {
            display: block;
            width: 100%;
            height: 14rem;
            object-fit: cover;
            object-position: top;
            opacity: 1;
            transition: 0.3s;
        }
    }

    .project-detail {
        padding: 1rem 1.5rem;
        flex-grow: 1;
        display: flex;
        flex-direction: column;

        .card-title {
            display: flex;
            align-items: center;

            .card-header {
                margin-right: auto;
            }
        }

        p {
            margin: 0 0 1rem 0;
            flex-grow: 1;
        }

        .project-source {
            display: inline-block;
            z-index: 1;

            svg {
                display: block;
                width: 24px;
                border-radius: 50%;
                padding: 2px;
                border: 1px solid transparent;
                transition: 0.2s;
            }
        }

        .project-skills {
            display: flex;
            flex-wrap: wrap;

            .project-skill {
                font-size: 0.75rem;
                border-radius: 4px;
                padding: 0.375rem 0.5rem;
                margin-right: 0.5rem;
                margin-bottom: 0.5rem;
                font-weight: 500;

                &.html {
                    color: #418d9b;
                }

                &.css {
                    color: #3e54a3;
                }

                &.js {
                    color: #e13784;
                }

                &.api {
                    color: #70941c;
                }
            }
        }
    }

    @media (max-width: 1100px) {
        .project-detail {
            .card-header {
                font-size: 1.125rem;
            }

            p {
                font-size: 0.9375rem;
            }
        }
    }

    @media (max-width: styles.$bp-2) {
        .project-image {
            img {
                height: 13rem;
            }
        }
    }

    @media (max-width: 800px) {
        .project-detail {
            padding: 1rem 1rem;
        }
    }

    @media (max-width: 600px) {
        .project-image {
            img {
                height: 14rem;
            }
        }
    }

    @media (max-width: styles.$bp-4) {
        .project-image {
            img {
                height: 12rem;
            }
        }
    }

    @media (max-width: styles.$bp-mobile) {
        .project-image {
            img {
                height: 10rem;
            }
        }

        .project-detail {
            padding: 0.75rem 1rem;
            
            .card-header {
                font-size: 1rem;
            }

            p {
                font-size: 0.875rem;
            }

            .project-source {
                font-size: 0.75rem;

                svg {
                    width: 20px;
                }
            }

            .project-skills .project-skill {
                font-size: 0.6875rem;
            }
        }
    }
}
