@use "../styles.scss";

.app.light-mode .hero {
  background-color: #e6ca2f;
  color: black;
}

.app.dark-mode .hero {
  background-color: #161618;
  color: white;
}

.hero {
  padding: 4rem 0 6rem 0;
  transition: 0.3s;

  .hero-container {
    display: flex;
    align-items: center;
  }

  .hero-text {
    display: inline-block;
    width: 55%;

    h1 {
      font-size: 4rem;
    }

    p {
      padding: 1rem 0;
      font-size: 1rem;
      max-width: 31rem;
    }
  }

  .hero-img {
    display: inline-block;
    text-align: center;
    width: 45%;

    img {
      width: 400px;
    }
  }

  @media (max-width: styles.$bp-2) {
    .hero-text {
      max-width: 87%;

      h1 {
        font-size: 3rem;
      }

      p {
        max-width: 87%;
      }
    }

    .hero-img {
      img {
        width: 315px;
      }
    }
  }

  @media (max-width: styles.$bp-3) {
    padding: 2rem 0 4rem 0;

    .hero-container {
      flex-direction: column;
    }

    .hero-text {
      width: 80%;
      text-align: center;
      order: 1;
      padding-top: 2rem;

      p {
        max-width: 100%;
      }
    }

    .hero-img {
      width: 100%;
    }
  }

  @media (max-width: styles.$bp-4) {
    .hero-text {
      h1 {
        font-size: 2.5rem;
      }
    }
  }

  @media (max-width: styles.$bp-mobile) {
    .hero-text {
      h1 {
        font-size: 2rem;
      }

      p {
        font-size: 0.875rem;
      }
    }
  }
}
