@use "../styles.scss";

.app.light-mode .header {
  background-color: #e6ca2f;

  .header-nav {
    .nav-link {
      color: black;

      &:hover::after {
        background: black;
      }
    }

    .nav-link-active {
      span {
        color: black;
      }

      &::after {
        background: black;
      }
    }

    .display-mode-toggle svg {
      fill: black;
    }
  }
}

.app.dark-mode .header {
  background-color: #161618;

  .header-nav {
    .nav-link {
      color: white;

      &:hover::after {
        background: white;
      }
    }

    .nav-link-active {
      span {
        color: white;
      }

      &::after {
        background: white;
      }
    }

    .display-mode-toggle svg {
      fill: white;
    }
  }
}

.header {
  transition: 0.3s;
}

.header .header-nav {
  text-align: right;
  padding: 1rem 0;

  a {
    text-decoration: none;
  }

  button {
    border: none;
    background-color: transparent;
    font: inherit;
  }

  .nav-link {
    display: inline-block;
    position: relative;
    padding: 1rem;
    cursor: pointer;

    span {
      font-size: 1rem;
      position: relative;
      bottom: 0;
      transition: all 0.3s, color 0s;
    }

    &:hover > span {
      bottom: 3px;
    }

    &::after {
      position: absolute;
      content: "";
      width: 80%;
      height: 2px;
      background: transparent;
      bottom: 0.5rem;
      left: 0;
      right: 0;
      margin: 0 auto;
      transition: 0.3s;
    }
  }

  .display-mode-toggle {
    .display-mode-text {
      display: inline-block;
      min-width: 90px;
    }
    svg {
      vertical-align: top;
    }
  }

  @media (max-width: styles.$bp-mobile) {
    text-align: center;

    .nav-link span {
      font-size: 0.875rem;
    }
  }
}
