@use "../styles.scss";

.about {
  .about-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 1.5rem;
    column-gap: 1.5rem;
  }

  @media (max-width: 1100px) {
    .about-grid {
      row-gap: 1rem;
      column-gap: 1rem;
    }
  }

  @media (max-width: styles.$bp-2) {
    .about-grid {
      grid-template-columns: 1fr 1fr;
    }
  }

  @media (max-width: 650px) {
    .about-grid {
      grid-template-columns: 1fr;
    }
  }
}
