.app.light-mode .footer {
  background-color: #e6ca2f;;
  color: black;

  .footer-nav .social {
    svg {
      fill: black;
    }

    &:hover::after {
      background: black;
    }
  }
}

.app.dark-mode .footer {
  background-color: #161618;
  color: white;

  .footer-nav .social {
    svg {
      fill: white;
    }

    &:hover::after {
      background: white;
    }
  }
}

.footer {
  text-align: center;
  transition: 0.3s;
  padding: 2rem 0;

  .footer-header {
    font-weight: 500;
    font-size: 1.25rem;
  }
  
  #copyright {
    font-size: 0.75rem;
  }
}

.footer .footer-nav {
  padding: 1rem 0;

  .social {
    display: inline-block;
    position: relative;
    padding: 0.5rem;
    margin: 0.5rem;
    width: 1.5rem;
    height: 1.5rem;

    span {
      position: relative;
      bottom: 0;
      transition: 0.3s;
    }
    &:hover span {
      bottom: 3px;
    }

    &::after {
      position: absolute;
      content: "";
      height: 2px;
      background: transparent;
      bottom: 0;
      left: 0;
      right: 0;
      margin: 0 auto;
      transition: 0.3s;
    }
  }
}
