@use "../styles.scss";

.projects {
  .project-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 2rem;
    column-gap: 2rem;
  }

  @media (max-width: styles.$bp-1) {
    .project-grid {
      grid-template-columns: 1fr 1fr;
    }
  }

  @media (max-width: styles.$bp-2) {
    .project-grid {
      row-gap: 1rem;
      column-gap: 1rem;
    }
  }

  @media (max-width: 600px) {
    .project-grid {
      grid-template-columns: 1fr;
      row-gap: 2rem;
      column-gap: 2rem;
    }
  }

  @media (max-width: styles.$bp-4) {
    .project-grid {
      row-gap: 1rem;
      column-gap: 1rem;
    }
  }
}
