@use "../styles.scss";

.about-card {
    padding: 1.5rem;

    &:hover {
        bottom: 2px;
      }

    .container {
        .card-title {
            display: flex;
            align-items: center;

            h2 {
                display: inline-block;
                flex-grow: 1;
            }

            svg {
                width: 24px;
                height: auto;
                padding-bottom: 0.375rem;
                display: inline-block;
            }
        }

        ul {
            padding: 0;

            ul li {
                margin-left: 1.5rem;
            }
        }

        li {
            padding-bottom: 0.5rem;
            margin-left: 1rem;
        }
    }

    @media (max-width: 1100px) {
        .container {
            .card-header {
                font-size: 1.125rem;
            }

            li {
                font-size: 0.9375rem;
            }
        }
    }

    @media (max-width: styles.$bp-3) {
        .container {
            .card-title svg {
                width: 20px;
            }
        }
    }

    @media (max-width: styles.$bp-mobile) {
        .container {
            .card-header {
                font-size: 1rem;
            }

            li {
                font-size: 0.875rem;
            }
        }
    }
}
