@use "../styles.scss";

.app {
    display: flex;
    flex-direction: column;
    min-width: 375px;

    .main-container {
        max-width: 75rem; //1200px
        margin: auto;
        padding: 0 3rem;

        @media (max-width: styles.$bp-3) {
            padding: 0 2rem;
        }

        @media (max-width: styles.$bp-mobile) {
            padding: 0 1.5rem;
        }
    }

    main {
        padding: 2rem 0;
        flex-grow: 1;

        .main-container {
            margin: 1rem auto 3rem auto;

            @media (max-width: styles.$bp-mobile) {
                margin: 0 auto 1rem auto;
            }
        }
    }

    .main-header {
        font-size: 2.5rem;
        margin-bottom: 2rem;
        display: inline-block;

        &::after {
            display: block;
            content: "";
            height: 2px;
            width: 100%;
            margin-top: 12px;
        }

        @media (max-width: styles.$bp-2) {
            font-size: 2rem;
        }

        @media (max-width: styles.$bp-mobile) {
            font-size: 1.625rem;
        }
    }
}

.app.dark-mode {
    main {
        transition: 0.3s;
        background-color: #2b2b2b;
    }

    .main-header {
        color: white;

        &::after {
            background-color: #787878;
        }
    }
}

.app.light-mode {
    main {
        transition: 0.3s;
        background-color: #cecccb;
    }

    .main-header {
        color: black;

        &::after {
            background-color: #999;
        }
    }
}
